<script>
  import Section from "./Section.svelte";
  import { fly, fade } from "svelte/transition";
  import { createEventDispatcher } from "svelte";
  import PortfolioItem from "./PortfolioItem.svelte";

  export let data;

  let openDetails = "";
  let overlay = "none";

  let dispatch = createEventDispatcher();

  function showprojectDetails() {
    dispatch("openProjectDetails", data);
  }
</script>

<style>
  .portfolio-item {
    position: relative;
    cursor: pointer;
  }

  .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.7);
  }
  .overlay > h3,
  .overlay > p {
    text-decoration: none;
    color: #fff;
    text-align: center;
  }
</style>

<div
  class="portfolio-item"
  on:mouseenter={() => {
    overlay = 'overlay';
  }}
  on:mouseleave={() => {
    overlay = 'none';
  }}
  on:click={showprojectDetails}>
  <img src={data.logo} alt="" />
  <h3>{data.title}</h3>
  {#if overlay === 'overlay'}
    <div class="overlay" transition:fade={{ y: 100, duration: 300 }}>
      <h3>Stack</h3>
      {#each data.technology as tech}
        <p>{tech}</p>
      {/each}
    </div>
  {/if}
</div>
