<script>
  import { createEventDispatcher } from "svelte";
  import Button from "../Ui/Button.svelte";

  let dispatch = createEventDispatcher();
  let x;
  let menu = false;
  function showMenu() {
    menu = true;
  }
</script>

<style>
  nav {
    position: relative;
    grid-area: 1/1/11/5;
    background: #111;
    width: 100%;
    height: 100%;
  }
  .nav-wrapper {
    display: grid;
    height: 100%;
    justify-content: center;
    align-content: center;
  }
  .menu-icon > img {
    position: absolute;
    top: calc((100% - 24px) / 2);
    left: calc((100% - 24px) / 2);
    width: 24px;
    height: auto;
    cursor: pointer;
  }
  .menu {
    display: block;
    height: 100vh;
    background: #fff;
  }
  @media (max-width: 980px) {
    nav {
      grid-area: 1/1/2/21;
      z-index: 6;
    }
    .nav-wrapper {
      grid-template-columns: repeat(5, 1fr);
      justify-content: stretch;
    }
  }
</style>

<svelte:window bind:innerWidth={x} />
{#if x < 500}
  <nav>

    {#if menu}
      <div class="menu">
        <Button
          mode="mobile-nav-btn"
          type="button"
          on:click={() => {
            menu = false;
            dispatch('showHome');
          }}>
          HOME
        </Button>
        <Button
          mode="mobile-nav-btn"
          type="button"
          on:click={() => {
            menu = false;
            dispatch('showResume');
          }}>
          RESUME
        </Button>
        <Button
          mode="mobile-nav-btn"
          type="button"
          on:click={() => {
            menu = false;
            dispatch('showPortfolio');
          }}>
          PORTFOLIO
        </Button>
        <Button
          mode="mobile-nav-btn"
          type="button"
          on:click={() => {
            menu = false;
            dispatch('showBlog');
          }}>
          BLOG
        </Button>
        <Button
          mode="mobile-nav-btn"
          type="button"
          on:click={() => {
            menu = false;
            dispatch('showContacts');
          }}>
          CONTACTS
        </Button>
      </div>
    {:else}
      <div class="menu-icon" on:click={showMenu}>
        <img src="./media/icons/menu.svg" alt="" />
      </div>
    {/if}
  </nav>
{:else}
  <nav>
    <div class="nav-wrapper">
      <Button
        mode="nav-btn"
        type="button"
        on:click={() => dispatch('showHome')}>
        HOME
      </Button>
      <Button
        mode="nav-btn"
        type="button"
        on:click={() => dispatch('showResume')}>
        RESUME
      </Button>
      <Button
        mode="nav-btn"
        type="button"
        on:click={() => dispatch('showPortfolio')}>
        PORTFOLIO
      </Button>
      <Button
        mode="nav-btn"
        type="button"
        on:click={() => dispatch('showBlog')}>
        BLOG
      </Button>
      <Button
        mode="nav-btn"
        type="button"
        on:click={() => dispatch('showContacts')}>
        CONTACTS
      </Button>
    </div>
  </nav>
{/if}
