<script>
  import { fly } from "svelte/transition";
  import { createEventDispatcher } from "svelte";
  import { portfolio } from "../Store/global-store.js";

  import Button from "../Ui/Button.svelte";
  import PortfolioItem from "./PortfolioItem.svelte";
  import Modal from "./Modal.svelte";
  import Section from "./Section.svelte";

  let portfolioitems = [];
  let projectDetails = "hide";
  let hovered = null;
  let openDetails = "";
  let selectedItem;
  let dispatch = createEventDispatcher();
  let unsubscribe = portfolio.subscribe(items => {
    portfolioitems = [...items];
  });

  let filters = ["All", "Online-Store", "Back-end", "Front-end", "API"];
  let title;
  let techs;
  let client;
  let role;
  let link;
  let completion;
  let description;
  function showDetails(data) {
    title = data.detail.title;
    techs = data.detail.technology;
    client = data.detail.client;
    role = data.detail.role;
    link = data.detail.link;
    completion = data.detail.completion;
    description = data.detail.description;
    projectDetails = "show";
  }

  function change() {
    console.log(" change");
  }
</script>

<style>
  #filters {
    display: inline-block;
    list-style: none;
  }

  #filters > li {
    display: inline-block;
    margin-right: 0.5rem;
    padding: 0.5rem 1rem;
    font-weight: 500;
    cursor: pointer;
    transition: 0.3s;
  }

  #filters > li:hover {
    color: #ccc;
  }

  .portfolio {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(100px, auto);
    grid-gap: 1rem;
    justify-items: center;
  }
  .project-details .technology,
  .project-details .client,
  .project-details .role,
  .project-details .completion,
  .project-details .web {
    margin: 1rem 0;
  }
  .project-details span {
    font-weight: 400;
    font-size: 1rem;
    margin-right: 1.3rem;
  }

  @media (max-width: 980px) {
    .portfolio {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  @media (max-width: 640px) {
    .portfolio {
      grid-template-columns: 1fr;
    }
    #filtersTitle {
      display: none;
    }
  }
</style>

<svelte:options immutable={true} />
<Section>
  <div slot="header">
    <h1>Portfolio</h1>
  </div>
  <div class="filters">
    <span id="filtersTitle">Filters:</span>
    <ul id="filters">
      {#each filters as filter}
        <li>{filter}</li>
      {/each}
    </ul>
  </div>
  <div class="portfolio">
    {#each portfolioitems as item, i (item.id)}
      <PortfolioItem
        data={item}
        {projectDetails}
        {selectedItem}
        on:openProjectDetails={showDetails} />
    {/each}
  </div>
</Section>

{#if projectDetails === 'show'}
  <Section>
    <div slot="header">
      <h3>{title}</h3>
    </div>
    <div class="project-details">
      <div class="technology">
        <span>Technology :</span>
        {#each techs as tech}{tech}&nbsp,{/each}
      </div>
      <div class="client">
        <span>Client :</span>
        {client}
      </div>
      <div class="completion">
        <span>Completion :</span>
        {completion}
      </div>
      <div class="role">
        <span>Role :</span>
        {role}
      </div>
      <div class="web">
        <span>Web :</span>
        <a href={link}>{link}</a>
      </div>
    </div>

    <div class="description">{description}</div>
    <div slot="footer">
      <Button
        on:click={() => {
          projectDetails = 'hide';
        }}
        mode="dark">
        Close
      </Button>
    </div>
  </Section>
{/if}
